import { Add, Delete, Remove } from "@mui/icons-material";
import { Box, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { addBasketItemAsync, removeBasketItemAsync } from "./basketSlice";
import { useAppDispatch, useAppSelector } from "../../app/store/configureStore";
import { BasketItem } from "../../app/layout/models/basket";
import { currencyFormat } from "../../app/util/util";


interface Props{
    items: BasketItem[];
    isBasket?: boolean;
}
export default function BasketTable({items, isBasket=true}:Props){

    const {status}=useAppSelector(state=>state.basket)
    const dispatch= useAppDispatch();
  
    return(
        <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} >
          <TableHead>
            <TableRow>
              <TableCell>Oprema</TableCell>
              <TableCell align="right">Cijena</TableCell>
              <TableCell align="center">Kolicina</TableCell>
              <TableCell align="right">Iznos</TableCell>
              {isBasket &&
              <TableCell align="right"></TableCell>}
            </TableRow>
          </TableHead>
          <TableBody>
            {items.map(item => (
              <TableRow
                key={item.productId}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                <TableCell component="th" scope="row">
                <Box display='flex' alignItems='center'>
                    <img src={item.slika} alt={item.ime} style={{height:50, marginRight:20}}/>
                    <span>{item.ime}</span>
                 </Box>
                </TableCell>
                <TableCell align="right">{(item.cijena)}</TableCell>
                <TableCell align="center">
                {isBasket &&
                  <LoadingButton color="error"
                  loading={status==='pendingRemoveItem'+item.productId+'rem'} onClick={()=>dispatch(removeBasketItemAsync({productId:item.productId, quantity:1,name:'rem'}))}>
                    <Remove/>
                  </LoadingButton>}
                  {item.quantity}
                  {isBasket &&
                  <LoadingButton
                   color="secondary"
                    loading={status==='pendingAddItem'+item.productId }
                     onClick={()=>dispatch(addBasketItemAsync({productId:item.productId}))}>
                    <Add/>
                  </LoadingButton>}
                  </TableCell>
                
                <TableCell align="right">{((item.cijena)*item.quantity)}KM</TableCell>
                {isBasket &&
                <TableCell align="right">
               <LoadingButton  color="error"
                 loading={status==='pendingRemoveItem'+item.productId+'del'}
                  onClick={()=>dispatch(removeBasketItemAsync({productId:item.productId, quantity:item.quantity, name:'del'}))}>
                <Delete/>
               </LoadingButton>
               </TableCell>}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    )
  
}