import { Divider, Grid, Table, TableBody, TableCell, TableContainer, TableRow, TextField, Typography } from "@mui/material";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Products } from "../../products";
import axios from "axios";
import { error } from "console";
import agent from "../../app/api/agent";
import NotFound from "../../app/errors/NotFound";
import LoadingComponent from "../../app/layout/LoadingComponent";
import { useAppDispatch, useAppSelector } from "../../app/store/configureStore";
import { addBasketItemAsync, removeBasketItemAsync } from "../basket/basketSlice";
import { LoadingButton } from "@mui/lab";

export default function KvadoviDetail(){
    const {basket, status}=useAppSelector(state=>state.basket);
    const dispatch=useAppDispatch();
    const {id}=useParams<{id:string}>();
    const[products, setProduct]=useState<Products | null> (null);
    const [loading, setLoading]=useState(true);
    const[quantity, setQuantity]=useState(0);
    const item=basket?.items.find(i=>i.productId===products?.id);

    useEffect(()=>{
        if(item) setQuantity(item.quantity);
       id && agent.Catalog.details(parseInt(id))
    .then(response=>setProduct(response))
    .catch(error=>console.log(error))
    .finally(()=>setLoading(false));
    }, [id, item])

    function handleInputChange(event:any){
        setQuantity(event.target.value);
        if(event.target.value>=0){
            setQuantity(event.target.value)
        }
    }
    function handleUpdateCard(){
       
        if(!item || quantity>item.quantity){
            const updateQuantity= item?quantity-item.quantity: quantity;
            dispatch(addBasketItemAsync({productId:products?.id!, quantity:updateQuantity}));
        }
        else{
            const updatedQuantity = item.quantity-quantity;
            dispatch(removeBasketItemAsync({productId:products?.id!, quantity:updatedQuantity}))
         
        }
    }
    if(loading) return <LoadingComponent message="Loading pro..."/>
    if(!products) return<NotFound/>
    return(
        <Grid container spacing={6}>
        <Grid item xs={6}>
            <img src={products.slika} alt={products.ime} width='100%'/>
        </Grid>
        <Grid item xs={6}>
            <Typography variant='h4'>
                {products.ime}
            </Typography>
            <Divider sx={{mb:2}}/>
            <Typography variant="h4" color='secondary'>
                {products.cijena.toFixed(2)}KM/DAN
            </Typography>
            <TableContainer>
                <Table>
                    <TableBody>
                        <TableRow>
                            <TableCell style={{fontWeight:"bold"}}>Ime</TableCell>
                            <TableCell>{products.ime}</TableCell>
                        </TableRow>
                    </TableBody>
                    <TableRow>
                        <TableCell style={{fontWeight:"bold"}}>Opis</TableCell>
                        <TableCell>{products.opis}</TableCell>
                    </TableRow>
                    
                    <TableRow>
                        <TableCell style={{fontWeight:"bold"}}>Brend</TableCell>
                        <TableCell>{products.karakteristike}</TableCell>
                    </TableRow>
                    <TableRow>
                        <TableCell style={{fontWeight:"bold"}}>Kolicina na stanju</TableCell>
                        <TableCell>{products.kolicinaNaStanju}</TableCell>
                    </TableRow>
                    
                </Table>
            </TableContainer>
            <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <TextField
                        onChange={handleInputChange}
                        variant="outlined"
                        type="number"
                        label='Kolicina u korpi'
                        fullWidth
                        value={quantity}/>

                    </Grid>
                    <Grid item xs={6}>
                        <LoadingButton
                        loading={status.includes('pendingRemoveItem'+item?.productId)}
                        onClick={handleUpdateCard}
                        disabled={item?.quantity === quantity || ! item && quantity===0}
                         sx={{height:'55px'}}
                        color="primary"
                        size="large"
                        variant="contained"
                        fullWidth>
                       

                            {item? 'Azuriraj korpu': 'Dodaj u korpu'}
                        </LoadingButton>
                    </Grid>
                </Grid>
    </Grid>
    </Grid>
            
            
    )
}