import { DarkMode, ShoppingCart } from "@mui/icons-material";
import { AppBar, Badge, Box, IconButton, List, ListItem, Switch, Toolbar, Typography } from "@mui/material";
import path from "path";
import { Link, NavLink } from "react-router-dom";
import { useStoreContext } from "../contex/StoreContext";
import { useAppSelector } from "../store/configureStore";
import SignedInMenu from "./SignedInMenu";

interface Props{
    darkMode : boolean;
    handleThemeChange:()=>void
}

const midLinks=[
    
    {title:'about', path:'/about'},
    {title:'contact', path:'/contact'},

]

const rightLinks=[
    {title:'prijava', path:'/prijava'},
    {title:'registracija', path:'/registracija'}
]

const navStyles={
    typography:'h6',
        textDecoration:'none',
        color:'inherit',
        
    '&:hover':{
        color:'#AFEAE3',
       
        
        
    },
    '&.active':{
        color:'text.secondary',
        
        
    },
}

export default function Header({darkMode, handleThemeChange}: Props){

    const {basket}=useAppSelector(state=>state.basket)
   const {user} = useAppSelector(state=>state.account);
    const itemCount= basket?.items.reduce((sum, item)=>sum+item.quantity, 0)
    
    return(
        <AppBar position="static" >
            <Toolbar sx={{display:'flex', justifyContent:'space-between', alignItems:'center'}}>
            <Box display='flex' alignItems='center'>
                <Typography variant="h6" component={NavLink}
                to='/' 
                sx={navStyles} >
                    IZNAJMLJIVANJE OPREME
                </Typography>
                <Switch checked ={darkMode} onChange={handleThemeChange}/>
                </Box>

                <List sx={{display:'flex'}}>
                <div className='menu-bar'>
                    <ul >
                    <li ><a><Typography variant="h6" marginTop={2.5} >KATALOG</Typography></a><li/>
                    <div className='sub-menu-1'>
                    <ul>
                    <Box display='flex'>
                     <li style={{backgroundImage: 'url(Images/sportovi/biciklizam.jpg', width:270.5, height: 150, fontSize:35, textAlign:'center', backgroundSize:'center'}}><a href='Catalog'>BICIKLIZAM</a></li>
                     <li style={{backgroundImage: 'url(Images/sportovi/skijanje.jpg', width:270.5, height:150, fontSize:35, textAlign:'center', backgroundSize:'center'}}><a href='CatalogSkijanje'>SKIJANJE</a></li>
                     </Box >
                 <Box display='flex'>
                 <li style={{backgroundImage: 'url(Images/sportovi/kajak.jpg', width:270.5, height:150, backgroundSize:'center',textAlign:'center', fontSize:35}}><a href='CatalogKajak'>KAJAK</a></li>
                    <li style={{backgroundImage: 'url(Images/sportovi/kvadovi.jpg', width:270.5, height:150, fontSize:35, textAlign:'center', backgroundSize:'center'}}><a href='CatalogKvadovi'>KVADOVI</a></li>
                </Box >

                <Box display='flex'>
                <li style={{backgroundImage: 'url(Images/sportovi/kampovanje.jpg', width:178, height:150, fontSize:18, textAlign:'initial', backgroundSize:'center'}}><a href='CatalogKampovanje'>KAMPOVANJE</a></li>
                <li style={{backgroundImage: 'url(Images/sportovi/trotinet.jpg', width:178,  backgroundSize:'center',textAlign:'center',fontSize:18 }}><a href='CatalogE_biciklo'>E-BICIKLO/TROTINET</a></li>
                <li style={{backgroundImage: 'url(Images/sportovi/planinarenje.jpg',height:150, width:178, backgroundSize:'center', fontSize:18}}><a href='CatalogPlaninarenje'>PLANINARENJE</a></li>
            </Box>
          </ul>
        </div>
        
        
        </li>
        
      </ul>
    
    

  </div>

                    
                    {midLinks.map(({title,path})=>(
                        <ListItem
                        component={NavLink}
                        to={path}
                        key={path}

                        sx={navStyles}>
                            {title.toUpperCase()}
                        </ListItem>

                        
                       
                    ))}
                </List>
                <Box display='flex' alignItems='center'>
                <IconButton component={Link} to='/basket' size="large" edge='start' color="inherit" sx={{mr:2}}>
                    <Badge badgeContent={itemCount} color="secondary" sx={navStyles} >
                        <ShoppingCart/>
                    </Badge>
                </IconButton>

               {user ?(
                <SignedInMenu/>
               ): (
                <List sx={{display:'flex'}}>
                    {rightLinks.map(({title,path})=>(
                        <ListItem
                        component={NavLink}
                        to={path}
                        key={path}

                        sx={navStyles}>
                            {title.toUpperCase()}
                        </ListItem>                      
                    ))}
                </List>
               )}              
               </Box>
            </Toolbar>
        </AppBar>
    )
}